.home-wrapper {
  padding: 20px 50px;
}

.home-table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  background-color: rgb(253, 253, 253);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.199);
}

.table-header {
  border-bottom: solid 1px #eee;
}

.table-row {
  border-bottom: solid 1px #eee;
  cursor: pointer;
}

.table-row:hover {
  background-color: #d4f0fd50;
}

.home-table th,
.table-data {
  text-align: center;
  width: 10%;
  padding: 15px 0;
}
.table-data2 {
  text-align: center;
  width: 10px;
  padding: 0px 0;
}

.home-title {
  margin: 20px;
}

.button-wrapper {
  display: flex;
  justify-content: center;
}

.mode{
  max-width: 500px;
  width:80%;
  margin: 0 auto;
  text-align: center;
}
.mode li{
  list-style-type: none;
  float:left;
  width:25%;
}
.mode li button{
  display:block;
  width:90%;
  height:50px;
  background-color: rgb(255, 255, 255);
  border:none;
  box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.452);
  cursor: pointer;
}
.clear{
  clear: left;
}